/*  Reset */
/*
test
*/
html, body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  font-size: 62.5%; }

div {
  box-sizing: border-box;
  position: relative; }

html {
  overflow: auto !important; }

body {
  zoom: 1; }

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  letter-spacing: 0px;
  box-sizing: border-box; }

a {
  cursor: pointer;
  text-decoration: none;
  box-sizing: border-box; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
  box-sizing: border-box; }

img {
  border: none; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after, q:before, q:after {
  content: "";
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

.container {
  width: 100%;
  height: 100%;
  position: relative; }

@font-face {
  font-family: "Montserrat-Medium";
  src: url("../fonts/Montserrat-Medium/Montserrat-Medium.eot");
  /* IE9 Compat Modes */
  src: url("../fonts/Montserrat-Medium/Montserrat-Medium.eot?#iefix") format("embedded-opentype"), url("../fonts/Montserrat-Medium/Montserrat-Medium.woff2") format("woff2"), url("../fonts/Montserrat-Medium/Montserrat-Medium.woff") format("woff"), url("../fonts/Montserrat-Medium/Montserrat-Medium.ttf") format("truetype"), url("../fonts/Montserrat-Medium/Montserrat-Medium.svg#svgMontserrat-Medium") format("svg");
  /* Legacy iOS */
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: "Roboto-Regular";
  src: url("../fonts/Roboto-Regular/Roboto-Regular.eot");
  /* IE9 Compat Modes */
  src: url("../fonts/Roboto-Regular/Roboto-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/Roboto-Regular/Roboto-Regular.woff2") format("woff2"), url("../fonts/Roboto-Regular/Roboto-Regular.woff") format("woff"), url("../fonts/Roboto-Regular/Roboto-Regular.ttf") format("truetype"), url("../fonts/Roboto-Regular/Roboto-Regular.svg#svgRoboto-Regular") format("svg");
  /* Legacy iOS */
  font-weight: normal;
  font-style: normal; }

.user {
  display: flex;
  align-items: center;
  cursor: pointer; }
  .user span {
    font-family: "Montserrat-Medium";
    font-size: 1.4rem;
    line-height: 1.7rem;
    color: #fff; }
  .user img {
    margin-right: 0.7rem; }

.switch-lang {
  display: inline-flex; }
  .switch-lang li {
    font-family: "Montserrat-Medium";
    font-size: 1.2rem;
    line-height: 1.5rem;
    color: #fff;
    width: 3.2rem;
    height: 3.2rem;
    justify-content: center;
    display: flex;
    align-items: center;
    border-radius: 50%;
    cursor: pointer; }
  .switch-lang .active {
    background-color: #ED2E5C; }

.h1 {
  font-family: "Montserrat-Medium";
  font-weight: 500;
  font-size: 4rem;
  line-height: 4.9rem;
  text-transform: uppercase;
  max-width: 44.2rem;
  color: #fff; }

.main-header {
  padding: 1.6rem 3rem;
  display: flex;
  flex-direction: row;
  background-color: #311795;
  justify-content: space-between;
  align-items: center; }

.header-right {
  display: inline-flex;
  align-items: center; }

.lang {
  margin-left: 3rem; }

.front-main {
  background-color: #311795;
  height: calc(100vh - 6.4rem);
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center; }

.popup {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  background-color: #F6F4FE;
  min-width: 60rem;
  min-height: 49.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0px 7px 20px rgba(0, 0, 0, 0.2);
  border-radius: 1.5rem;
  opacity: 0;
  z-index: -20;
  position: absolute; }

.wrapBlur {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0;
  z-index: -20;
  position: absolute; }

.popup-wrap {
  min-height: 34.9rem; }

.form-popup {
  opacity: 0;
  z-index: -20;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%); }

.form-popup.show {
  position: relative;
  transform: translateX(0) translateY(0);
  top: 0;
  left: 0; }

.close {
  position: absolute;
  right: 1.6rem;
  top: 1.6rem;
  cursor: pointer; }

.form-title {
  font-family: "Roboto-Regular";
  font-weight: 500;
  font-size: 3.6rem;
  line-height: 3.2rem;
  color: #341A9B;
  margin-bottom: 3.2rem; }

.input-title {
  font-family: "Roboto-Regular";
  font-size: 1.4rem;
  line-height: 1.6rem;
  display: block;
  color: #341A9B;
  margin-bottom: 1.1rem;
  padding-left: 1.8rem; }

.input-wrap {
  margin-bottom: 2.4rem; }

.form-input {
  border: 0.1rem solid #341A9B;
  border-radius: 0.6rem;
  width: 100%;
  height: 4.2rem;
  padding-left: 1.8rem;
  font-family: "Roboto-Regular";
  font-size: 1.4rem;
  line-height: 1.6rem;
  color: #858585;
  max-width: 33.5rem; }

.form-input::placeholder {
  font-family: "Roboto-Regular";
  font-size: 1.4rem;
  line-height: 1.6rem;
  color: #858585; }

.form-input:focus {
  outline: none; }

.form-submit {
  background-color: #341A9B;
  border-radius: 0.6rem;
  font-family: "Roboto-Regular";
  font-size: 1.6rem;
  line-height: 1.9rem;
  text-align: center;
  color: #fff;
  padding: 1.1rem 5.1rem;
  cursor: pointer;
  border: none;
  margin-top: 0.8rem; }

.error {
  font-family: "Roboto-Regular";
  font-size: 1.4rem;
  line-height: 1.6rem;
  color: #EA0000;
  position: relative;
  top: 1rem; }

.new-pass {
  font-family: "Roboto-Regular";
  font-size: 1.4rem;
  line-height: 1.6rem;
  color: #858585;
  text-align: center;
  margin-top: 6.2rem;
  cursor: pointer; }

.form-description {
  font-family: "Roboto-Regular";
  font-size: 1.4rem;
  line-height: 1.6rem;
  text-align: center;
  color: #341A9B;
  margin: 0 auto;
  max-width: 30.7rem;
  margin-bottom: 3.2rem; }

.sendFull {
  font-size: 2.4rem;
  line-height: 2.8rem;
  text-align: center;
  color: #27AE60; }

.sendEntrance {
  font-size: 1.6rem;
  line-height: 1.9rem;
  color: #341A9B; }

.register {
  background-color: transparent;
  color: #341A9B;
  margin-left: 2.3rem;
  padding-left: 0;
  padding-right: 0;
  position: relative; }

.underline:after {
  content: "";
  width: 100%;
  height: 0.1rem;
  opacity: 0;
  background-color: #341A9B;
  position: absolute;
  left: 0;
  bottom: 0rem;
  transform: translateY(0rem); }

.underline:hover:after {
  content: "";
  width: 100%;
  opacity: 1;
  height: 0.1rem;
  background-color: #341A9B;
  position: absolute;
  left: 0;
  bottom: 0rem;
  transform: translateY(-1.1rem);
  transition: opacity 500ms ease, transform 500ms ease; }

.show {
  opacity: 1;
  z-index: 100; }

@media screen and (min-width: 1921px) {
  html, body {
    font-size: 100%; } }
